import React from 'react';
import { AuthContext } from "Boot/auth/AuthProvider";
import { getToken } from "lib/helpers/localStorage";
import first from "lodash/first";
import isEmpty from "lodash/isEmpty";
import isNil from "lodash/isNil";
import map from "lodash/map";
import { useContext, useEffect } from "react";
import { connect } from "react-redux";
import { Route, Switch, withRouter } from "react-router-dom";
import { Container, Row } from "reactstrap";
import { updateImages as UpdateImages, get, update } from "state/Auth/Epic";
import { selectItem } from "state/Auth/Selectors";
import DashboardMainMenu from "../../components/DashboardMainMenu";
import BaseIndexPage from "../../components/HOC/BaseIndexPage";
import BankAccount from "./Components/BankAccount/index";
import Edit from "./Components/EditDetails/index";
import Photo from "./Components/EditProfilePhoto/index";
import VerifyDetails from "./Components/VerifyDetails";
import VerticalMenu from "./Components/VerticalMenu";

const Index = ({
  match: { path },
  onSubmitItem,
  UpdateImages,
  getItem,
  ...props
}) => {
  const { user, tokenAuth } = useContext(AuthContext);

  const updateLocalData = (data) => {
    const token = getToken();
    const item = { userData: data, token: token };
    tokenAuth(item);
  };

  const handleSubmit = (values) => {
    const { avatar } = values;

    let { REACT_APP_S3_BUCKET_URL } = process.env;

    if (!isNil(avatar)) {
      let display_id = Math.floor(Date.now() / 1000);

      let avatar_images = map(avatar, ({ name, ...rest }, index) => {
        let fileName = name;
        let sub_location = `${display_id}_profile_photo_${
          index + 1
        }${fileName.substring(fileName.lastIndexOf("."))}`;
        return {
          location: `${REACT_APP_S3_BUCKET_URL}/${sub_location}`,
          sub_location,
          type: "profile_photo",
          name: name,
        };
      });

      values = {
        ...values,
        avatar: first(avatar_images),
      };

      onSubmitItem({ id: user._id, ...values }, ({ data }) => {
        updateLocalData(data);
        const { error = "" } = data;

        if (isEmpty(error)) {
          // let profilePhoto = await Compression(profile_photo);

          const Images = {
            entity_type: "user",
            file_first_name: `${display_id}_`,
            entity_id: user._id,
            profile_photo_url: data.avatar,
            profile_photo: avatar,
          };

          UpdateImages(user._id, Images)
            .then((res) => {})
            .catch((err) => {});
        }
      });
    } else {
      onSubmitItem({ id: user._id, ...values }, ({ data, ...rest }) => {
        updateLocalData(data);
      });
    }
  };

  useEffect(() => {
    getItem(user._id);
  }, []);
  return (
    <div className=" mg-top-90 ">
      <DashboardMainMenu />
      <Container>
        <Row className="py-4">
          <VerticalMenu />
          <Switch>
            <Route exact path={path}>
              <Edit onSubmit={handleSubmit} {...props} />
            </Route>
            <Route exact path={`${path}/photo`}>
              <Photo onSubmit={handleSubmit} {...props} />
            </Route>
            <Route exact path={`${path}/verify`}>
              <VerifyDetails {...props} />
            </Route>
            <Route exact path={`${path}/back-accounts`}>
              <BankAccount onSubmit={handleSubmit} {...props} />
            </Route>
          </Switch>
        </Row>
      </Container>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    noHeader: true,

    noBanner: true,
    item: selectItem(state),
  };
};
const mapDispatchToProps = (dispatch) => ({
  get: (params) => dispatch(get(params)),
  update: (id, params) => dispatch(update(id, params)),
  UpdateImages: (id, params) => dispatch(UpdateImages(id, params)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(BaseIndexPage(Index)));
