import UserCard from "components/section-components/UserCard/UserCard";
import React from "react";
import { withRouter } from "react-router";
import { NavLink } from "react-router-dom";
import { Col } from "reactstrap";
import { BACK_ACCOUNT, PHOTO, USER_DETAIL, VERIFY } from "./Constants";

const VerticalMenu = ({ match: { path }, ...props }) => {
  const {
    location: { pathname },
  } = props;
  const activeclass = {
    color: "rgb(255, 255, 255)",
    transition: "all 0.5s ease",
    backgroundColor: "#40586d",
  };
  return (
    <Col md="3">
      <div className="userDetailsMenu userDetailsMenuDesktop">
        <ul className="vertical-menu__list">
          <UserCard />
          <li className=" ">
            <NavLink
              activeStyle={pathname === USER_DETAIL && activeclass}
              to={USER_DETAIL}
              target=""
            >
              Edit Details
            </NavLink>
          </li>
          <li className="active ">
            <NavLink
              activeStyle={activeclass}
              to={`${path}${VERIFY}`}
              target=""
            >
              Verify Details
            </NavLink>
          </li>
          <li className=" ">
            <NavLink activeStyle={activeclass} to={`${path}${PHOTO}`} target="">
              My Photo
            </NavLink>
          </li>
          <li className=" ">
            <NavLink
              activeStyle={activeclass}
              to={`${path}${BACK_ACCOUNT}`}
              target=""
            >
              Bank Accounts
            </NavLink>
          </li>
        </ul>
      </div>
    </Col>
  );
};

export default withRouter(VerticalMenu);
